%flexRow {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.numbers {
  width: 100%;
  height: 100px;
  margin: 40px 0 50px 0;
  @extend %flexRow;

  div {
    width: 100px;
    border-bottom: 2px solid #212529;
    text-align: center;
    font-size: 80px;
    font-weight: 600;
  }
}

.form {
  @extend %flexRow;

  input {
    width: 250px;
    height: 50px;
    margin-right: 5px;
    padding-left: 10px;
    border-radius: 5px;
    border: solid 1px #212529;
  }

  button {
    margin: 0px;
    width: 75px;
    height: 50px;
    border-radius: 5px;
    border: none;
    color: white;
    font-weight: 600;
    background-color: #212529;
    transition: all 0.3s;
  }
}

.count {
  margin-top: -5px;
  
  p {
  margin: 0px;
  font-size: 21px;
  }

  .red {
    color : #c22727
  }
}

.table {
  font-size: 30px;
  margin-top: 30px;

  b {
    letter-spacing: 2px
  }

  .strike {
    text-align: right;
  }
}


